.App {
  text-align: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700&display=swap');

/* GENERAL */
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #282a2f;
}

Link, a {
  text-decoration: none;
}

li, p {
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 13px;
  color: #edf0f1;
  text-decoration: none;
  margin: 0;
  list-style: none;
}

p {
  margin: 5px;
}

img {
  width: 100%;
}

.loader, .signup, .prod-detail_card {
  text-align: center;
  color: #edf0f1;
}

/* ECOMMERCE */
.goHome {
  display: flex;
  justify-content: center;
  align-items: center;
}

.goHome_button {
  margin: 10px auto;
  width: 40%;
  display: flex;
  justify-content: center;
}

/* NAVBAR */
.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 5%;
  background-color: #25242a;
  position: fixed;
  top: 0;
  width: 90%;
  z-index: 1;
}

.header_alt {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0 5%;
  background-color: #25242a;
  opacity: 0;
  margin-bottom: 2%;
}

.nav_links {
  list-style: none;
  margin: 5px 0 10px;
}

.nav_links li {
  display: inline-block;
  padding: 0px 20px;
}

.nav_links li {
  transition: all 0.3s ease 0s;
}

.signup-t {
  color: #0088a9;
}

.nav_links .signup-t:hover {
  color: #edf0f1;
}

.nav_links li:hover {
  color: #0088a9;
  transition: all 0.3s ease 0s;
  cursor: pointer;
}

.nav_logo {
  cursor: pointer;
  margin-right: auto;
  font-weight: bold;
  font-size: 15px;
}

/* TITULOS */
.home_title, .prod-detail_title {
  text-align: center;
  margin: 0 auto 25px;
  font-size: 20px;
}

/* PRODUCTO */
.prod_card {
  background-color: #edf0f1;
  display: flex;
  width: 30%;
  margin: 0 auto 30px;
  padding: 10px;
  border-radius: 15pt;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 5px 1px black;
  transition: all 0.2s ease 0s;
}

.prod_card:hover {
  box-shadow: 0px 0px 8px 1px black;
}

.prod_img {
  mix-blend-mode: multiply;
  width: 50%;
  transition: all 0.3s ease 0s;
  margin: 10px;
}

.prod_img:hover {
  transform: scale(1.05);
}

.prod_text {
  width: 50%;
  color: #282a2f;
  margin: 10px;
  display: flex;
  flex-direction: column;
}

.prod_price, .prod_name, .log_title {
  color: #282a2f;
}

.prod_price {
  font-weight: 500;
  font-size: 13pt;
}

.prod_name {
  font-size: 9pt;
}

.prod_button {
  background-color: #282a2f;
  border: none;
  color: #edf0f1;
  padding: 5px 3px;
  margin: 2px;
  font-family: 'Inter', sans-serif;
  width: 100%;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  font-size: 10pt;
}

.prod_button:hover {
  background-color: #0088a9;
}


[data-title]:hover:after {
  opacity: 1;
  transition: all 0.1s ease 0.5s;
  visibility: visible;
}

[data-title]:after {
  content: attr(data-title);
  background-color: #25242a;
  color: #fff;
  font-size: 8pt;
  font-family: Inter;
  position: absolute;
  padding: 5px 10px;
  bottom: -1.6em;
  left: 100%;
  white-space: nowrap;
  opacity: 0;
  z-index: 99999;
  visibility: hidden;
  border-radius: 8pt;
  
}
[data-title] {
  position: relative;
}

/* DETALLE PRODUCTO */
.prod-detail_card {
  background-color: #edf0f1;
  display: flex;
  width: 50%;
  margin: 0 auto 30px;
  padding: 10px;
  border-radius: 15pt;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 5px 1px black;
  transition: all 0.2s ease 0s;
}

.prod-detail_card:hover {
  box-shadow: 0px 0px 10px 1px black;
}

.prod-detail_img {
  mix-blend-mode: multiply;
  width: 60%;
  transition: all 0.3s ease 0s;
  margin: 10px;
}

.prod-detail_img:hover {
  transform: scale(1.05);
}

.prod-detail_text {
  width: 40%;
  color: #282a2f;
  margin: 10px;
  display: flex;
  flex-direction: column;
}

.prod-detail_price, .prod-detail_name, .prod-detail_description, .prod-detail_stock, .prod-detail_sales {
  color: #282a2f;
}

.prod-detail_price {
  font-weight: 500;
  font-size: 13pt;
}

.prod-detail_name {
  font-size: 13pt;
  font-weight: 500;
  text-decoration: underline;
}

.prod-detail_description {
  font-size: 9pt;
}

.prod-detail_stock, .prod-detail_sales {
  font-size: 8pt;
  opacity: 0.5;
}

.prod-detail_category {
  text-transform: uppercase;
}

.prod-detail_button {
  border: none;
  color: #edf0f1;
  padding: 8px 3px;
  margin: 1px;
  font-family: 'Inter', sans-serif;
  width: 100%;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  font-size: 10pt;
}

.prod-detail_button:hover {
  background-color: #017997;
}

/* EDITAR DETALLE */
.tool_card {
  background-color: #edf0f1;
  display: flex;
  width: 40%;
  margin: 0 auto 30px;
  padding: 10px;
  border-radius: 15pt;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 5px 1px black;
  transition: all 0.2s ease 0s;
}

.tool_card:hover .tool_card:hover {
  box-shadow: 0px 0px 8px 1px black;
}

.tool_input {
  border: none;
  outline: none;
  width: 80%;
  border-radius: 10pt;
  padding: 10px;
  margin: 10px;
  text-align: center;
  font-size: 12px;
}

.tool_save {
  background-color: #0088a9;
  border: none;
  color: #edf0f1;
  padding: 8px 3px;
  margin: 1px;
  font-family: 'Inter', sans-serif;
  width: 50%;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  font-size: 10pt;
}

.tool_form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.tool_card {
  width: 30%;
  position: absolute;
}

.tool {
  width: 100%;
}

.tool_form {
  flex-direction: column;
}

.delete_card {
  background-color: green;
  width: 30%;
  margin: 0 auto 30px;
  padding: 10px;
  border-radius: 10pt;
  transition: all 0.2s ease 0s;
  font-size: 12pt;
  color: white;
  text-align: center;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

input[type=number] { 
  -moz-appearance: textfield
}

label {
  font-weight: 500;
}

/* TOOLS */
.tool_btn_card {
  width: 30%;
  display: flex;
  margin: 10px auto;
  justify-content: center;
  margin: 0 auto 10px;
  padding: 10px;
  border-radius: 15pt;
  justify-content: center;
}

.tools_card {
  display: flex;
  justify-content: center;
}

.tool_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45%;
  border-radius: 10px;
  background-color: #0088a9;
  color: #edf0f1;
  border: none;
  padding: 5px 3px;
  margin: 0px 10px 0px;
  font-family: 'Inter', sans-serif;
  font-size: 10pt;
  cursor: pointer;
  transition: all 0.3s ease 0s;
}

.tool_btn:hover {
  background-color: #017997;
}

/* LOGIN SIGNUP */
.log_card {
  background-color: #edf0f1;
  display: flex;
  flex-direction: column;
  width: 25%;
  margin: 0 auto 30px;
  padding: 30px 10px;
  border-radius: 15pt;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 5px 1px black;
  transition: all 0.2s ease 0s;
}

.log_card:hover {
  box-shadow: 0px 0px 15px 2px black;
}

.log_form {
  width: 80%;
}

.log_title {
  font-size: 25px;
  font-weight: 500;
  text-decoration: underline;
}

.log_field {
  margin: 10px auto;
}

.log_field input {
  width: 100%;
  margin: 10px 0;
  padding: 0 5px 0;
  font-size: 10pt;
  border: none;
  border-bottom: 1px solid #282a2f;
  background: none;
  outline: none;
  font-family: 'Inter', sans-serif;
  color: #282a2f;
  transition: all 0.5s ease 0s;
}

.log_field input:hover {
  border-bottom: 1px solid #0088a9;
}

.log_field input:focus,
.log_field input:valid {
  border-bottom: 2px solid #0088a9;
}

::placeholder {
  color: #282a2f;
  opacity: 0.5;
}

.log_pass {
  color: #282a2f;
  cursor: pointer;
  font-family: 'Inter', sans-serif;
  font-size: 10pt;
  text-align: center;
  transition: .5s;
  margin: 10px auto;
}

.log_pass:hover {
  color: #0088a9;
}

.log_btn {
  background-color: #0088a9;
  border: none;
  color: #edf0f1;
  padding: 5px 3px;
  margin: 10px auto;
  font-family: 'Inter', sans-serif;
  width: 50%;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  font-size: 10pt;
  display: flex;
  justify-content: center;
  align-items: center;
}

.log_button:hover {
  background-color: #017997;
}

.log_button a {
  color: #edf0f1;
  text-decoration: none;
}

.log_signup {
  color: #282a2f;
  font-family: 'Inter', sans-serif;
  font-size: 10pt;
  text-align: center;
  text-decoration: none;
  user-select: none;
  list-style: none;
  margin: 10px auto;
}

.log_signup a {
  color: #0088a9;
  transition: all 0.3s ease 0s;
}

.log_signup a:hover {
  text-decoration: underline;
}

/* RESPONSIVE */
@media screen and (max-width: 600px) {
  /* ECOMMERCE */
  .goHome_button {
    width: 80%;
  }

  /* HOME */
  .home_title {
    margin: 15px auto 15px;
  }

  .header {
    display: flex;
    flex-direction: column;
    position: relative;
  }

  .header_alt {
    display: none;
  }

  .nav_logo {
    cursor: pointer;
    margin: 10px 0 0px;
  }

  .nav_links {
    text-align: center;
    padding: 0;
  }

  /* PRODUCTO */
  .prod_card {
    margin: 0 auto 15px;
    width: 85%;
    flex-direction: column;
  }

  .prod_img, .prod_text {
    width: 80%;
    margin: 5px;
    text-align: center;
  }

  .prod_price {
    margin: 0;
  }

  /* DETALLE */
  .prod-detail_card {
    width: 85%;
    flex-direction: column;
  }

  .prod-detail_title {
    margin: 15px auto 15px;
    flex-direction: column;
  }

  .prod-detail_img {
    width: 80%;
  }

  .prod-detail_text {
    width: 95%;
  }

  /* LOGIN */
  .log_card {
    width: 90%;
    margin: 15px auto 15px;
    padding: 30px 0;
  }

  .log_button {
    width: 80%;
    margin: 10px auto;
  }

  .tool_btn {
    width: 70%;
    margin: 0 3px 0;
  }

  .tool_btn_card {
    width: 85%;
  }

  .tool_card {
    width: 85%;
  }

  .delete_card {
    width: 70%;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
